import './scss/Projet.scss';


export default function Projet({id, titre, desc, roles}) 
{
  return (
    <article className="Projet">
      <div className="Screenshot">
        <img draggable="false" src={process.env.PUBLIC_URL + `/images/${id}.png`} alt=''/>
      </div>
      <div className="info">
        <h2>{titre}</h2>
        <p>Rôles : {roles}</p>
        <p>{desc}</p>
      </div>
      
    </article>
  );
}